import Accordion from 'accordion-js';
import 'accordion-js/dist/accordion.min.css';
import Swiper from 'swiper';
import { Navigation } from 'swiper/modules';
import 'swiper/css';

function initAboutMeAccordion() {
  new Accordion('.about-me__accordion-container', {
    duration: 400,
    showMultiple: false,
    collapse: true,
  });
}

initAboutMeAccordion();

const accordionHeaders = document.querySelectorAll('.about-me__item--box');
accordionHeaders.forEach(header => {
  header.addEventListener('click', function () {
    const accordionItem = this.closest('.about-me__item');
    const panel = accordionItem.querySelector('.about-me__panel');

    if (panel) {
      panel.classList.toggle('visually-hidden');
    }
  });
});

document.querySelectorAll('.about-me__button').forEach(button => {
  button.addEventListener('click', function () {
    this.querySelector('.about-me__icon').classList.toggle(
      'about-me__icon--rotate'
    );
  });
});

// SWIPERJS

const mySwiper = new Swiper('.swiper-skills-list', {
  modules: [Navigation],
  spaceBetween: 0,
  loop: true,
  loopSlides: 6,
  keyboard: {
    enabled: true,
  },
  mousewheel: {
    invert: true,
  },
  breakpoints: {
    320: {
      slidesPerView: 2,
    },
    768: {
      slidesPerView: 3,
    },
    1440: {
      slidesPerView: 5,
    },
  },
  navigation: {
    nextEl: '.swiper-skill-list-next',
  },
});

mySwiper.on('slideChange', function onSlideChange() {
  mySwiper.slides.forEach(slide => {
    slide.classList.add('color-slide');
    slide.classList.remove('color-slide');
  });
});
